                                            </div>
                                            <draggable v-model="thirdItem.children" element="div" @end="dragEnd" :move="onMoveCallback" :options="{animation:150,draggable:'.fourth-chapter-box'}">
                                                <div class="resourcebox fourth-chapter-box" v-for="fourthItem in thirdItem.children" :key="fourthItem.id">
                                                    <div class="knowledge">
                                                        <span class="chlft">
                                                            {{fourthItem.name}}
                                                            <span class="chline">|</span>ID{{fourthItem.resource && fourthItem.resource.id}} {{fourthItem.resource && fourthItem.resource.discriminator | Resource2chn}} {{fourthItem.resource && fourthItem.resource.title}} </span>
                                                        <span class="chrgt" @click="openeEditResource(fourthItem)"></span>
                                                        <span class="chrgt" @click="openDelResDialog(fourthItem.id)"></span>
                                                    </div>
